<template>
    <div>
        <div style="display: flex;justify-content: space-between">
            <div>
              <el-input placeholder="请输入员工名或工号进行搜索，按回车搜索..." prefix-icon="el-icon-search"
                  clearable
                  @clear="searchSal"
                  style="width: 350px;margin-right: 10px" v-model="keyword"
                  @keydown.enter.native="searchSal" :disabled="showAdvanceSearchView">
              </el-input>
                <el-button icon="el-icon-search" type="primary" @click="searchSal" :disabled="showAdvanceSearchView">
                搜索
                </el-button>
            </div>
            <div>
             <el-upload
                :show-file-list="false"
                :before-upload="beforeUpload"
                :on-success="onSuccess"
                :on-error="onError"
                :disabled="importDataDisabled"
                style="display: inline-flex;margin-right: 8px"
                action="/employee/basic/import">
               <el-button :disabled="importDataDisabled" type="success" :icon="importDataBtnIcon">
                   {{importDataBtnText}}
               </el-button>
             </el-upload>
              <el-button type="success" @click="exportData" icon="el-icon-download">
                  导出数据
              </el-button>
            </div>
        </div>
        <div style="margin-top: 10px">
            <el-table :data="saltable" border stripe size="mini">
                <el-table-column type="selection" align="left" width="55"></el-table-column>
                <el-table-column prop="eid" label="员工号" width="" align="left"></el-table-column>
                <el-table-column prop="name" label="员工姓名" width="" align="left"></el-table-column>
                <el-table-column prop="basicSal" label="基本工资" width="" align="left"></el-table-column>
                <el-table-column prop="bonus" label="奖金" width="" align="left"></el-table-column>
                <el-table-column prop="benefit" label="津贴" width="" align="left"></el-table-column>
                <el-table-column prop="overtime" label="加班" width="" align="left"></el-table-column>
                <el-table-column prop="insurance" label="五险一金" width="" align="left"></el-table-column>
                <el-table-column prop="attend" label="出勤天数" width="" align="left"></el-table-column>
                <el-table-column prop="addSal" label="合计金额" width="" align="left"></el-table-column>
                <el-table-column prop="realSal" label="实发金额" width="" align="left"></el-table-column>
                <el-table-column prop="remake" label="备注" width="" align="left"></el-table-column>
                <el-table-column
                        fixed="right"
                        width="200"
                        label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button @click="editSaltable(scope.row)" >编辑</el-button>
                        <el-button @click="deleteSaltable(scope.row)" type="danger">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SalTable",
        data(){
            return {
                importDataBtnText: '导入数据',
                importDataBtnIcon: 'el-icon-upload2',
                importDataDisabled: false,
                saltable:[]
            }
        },
        mounted() {
            this.initsaltable()
        },
        methods: {
            onError(err, file, fileList) {
                this.importDataBtnText = '导入数据';
                this.importDataBtnIcon = 'el-icon-upload2';
                this.importDataDisabled = false;
            },
            onSuccess(response, file, fileList) {
                this.importDataBtnText = '导入数据';
                this.importDataBtnIcon = 'el-icon-upload2';
                this.importDataDisabled = false;
                this.initEmps();
            },
            beforeUpload() {
                /*
                  *上传前，判断格式
                 */
                this.importDataBtnText = '正在导入';
                this.importDataBtnIcon = 'el-icon-loading';
                this.importDataDisabled = true;
            },
            exportData() {
                window.open('/salary/table/export', '_parent');
            },
            initsaltable(){
                this.getRequest("/sal/table/").then(resp=>{
                    if(resp){
                        this.saltable=resp;
                    }
                })

            }
        }
    }
</script>

