<template>
    <div>
        <el-input placeholder="请输入关键字进行搜索..." prefix-icon="el-icon-search"
                  clearable
                  @clear="SalMonth"
                  style="width: 350px;margin-right: 10px" v-model="keyword"
                  @keydown.enter.native="SalMonth" :disabled="showAdvanceSearchView"></el-input>
        <el-button icon="el-icon-search" type="primary" @click="SalMonth" :disabled="showAdvanceSearchView">
            搜索
        </el-button>
        <div style="margin-top: 10px">
        <el-table :data="salmonth" border stripe size="mini">
            <el-table-column prop="" label="1" width="" align="left"></el-table-column>
            <el-table-column prop="" label="2" width="" align="left"></el-table-column>
            <el-table-column prop="" label="3" width="" align="left"></el-table-column>
            <el-table-column prop="" label="4" width="" align="left"></el-table-column>
            <el-table-column prop="" label="5" width="" align="left"></el-table-column>
            <el-table-column prop="" label="6" width="" align="left"></el-table-column>
            <el-table-column prop="" label="7" width="" align="left"></el-table-column>
            <el-table-column
                    fixed="right"
                    width="200"
                    label="操作" align="center">
                <template slot-scope="scope">
                    <el-button @click="editSalmonth(scope.row)" >编辑</el-button>
                    <el-button @click="deleteSalmonth(scope.row)" type="danger">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SalMonth",
        data(){
            return{
                salmonth:[]
            }
        },
        mounted() {
            this.initsalmonth()
        },
        methods:{
            initsalmonth(){
                this.getRequest("/sal/month/").then(resp=>{
                    if(resp){
                        this.salmonth=resp;
                    }
                })
            }
        }
    }
</script>

